import React, { Component } from 'react';
import Cropper from 'react-cropper';
import 'cropperjs/dist/cropper.css';
export default class App extends Component {
    constructor(props) {
        super(props);
        this.state = {
            src: 'https://gss0.baidu.com/70cFfyinKgQFm2e88IuM_a/forum/w=580/sign=3b900475252eb938ec6d7afae56385fe/61954b10b912c8fcacb9f159f6039245d78821fd.jpg',
            newSrc: null,
            height: 400
        };
    }

    _crop() {
        // image in dataUrl
        let canvasImg = this.cropper.getCroppedCanvas({
            fillColor: '#fff',
            imageSmoothingQuality: 'high'
        }),
            newSrc = canvasImg.toDataURL()
        // canvasImg.toBlob((blob)=>{
        //     console.log(blob)
        // })
        this.setState({
            newSrc
        })
    }
    throttle(func, delay = 300) {
        let { timer } = this
        if (!timer) {
            this.timer = setTimeout(() => {
                func();
                this.timer = null;
            }, delay);
        }
    }
    onCropperInit(cropper) {
        this.cropper = cropper;
    }
    onSelectFile = e => {
        if (e.target.files && e.target.files.length > 0) {
            const reader = new FileReader();
            reader.addEventListener("load", () =>
                this.setState({ src: reader.result })
            );
            console.log(reader.result);
            reader.readAsDataURL(e.target.files[0]);
        }
    };
    render() {
        let { height } = this.state
        return <div>
            <div>
                <input type="file" onChange={this.onSelectFile} />
            </div>
            <div style={{ display: 'flex' }}>
                <Cropper
                    src={this.state.src}
                    style={{ width: '500px', height: height + 'px' }}
                    // Cropper.js options
                    initialAspectRatio={1 / 1}
                    dragMode='crop'
                    viewMode={0}
                    background
                    guides
                    crop={this._crop.bind(this)}
                    onInitialized={this.onCropperInit.bind(this)}
                />
                <img id="preview" style={{ height: height + 'px', display: 'inline-block', marginLeft: '20px' }} src={this.state.newSrc} alt="" />
            </div>
        </div>
    }
}
