import App from '../components/App'
import Editor from '../components/editor'
import ReEditor from '../components/re-editor'
import ReactCropper from '../components/react-cropper'
import loginValid from '../components/loginValid'
import imgLazyload from '../components/imgLazyload'

let routes = [
    {
        title: 'App',
        path: '/',
        component: App
    },
    {
        title: '裁剪',
        path: '/reactCropper',
        component: ReactCropper
    },
    {
        title: '拼图验证',
        path: '/loginValid',
        component: loginValid
    },
    {
        title: 'Editor',
        path: '/Editor',
        component: Editor
    },
    {
        title: 'ReEditor',
        path: '/ReEditor',
        component: ReEditor
    },
    {
        title: 'imgLazyload',
        path: '/imgLazyload',
        component: imgLazyload
    }
]
export default routes