import React, { Component } from "react";
import ImageCode from './imgCode'
function getImage() {
    let imgs = ['https://gss0.baidu.com/70cFfyinKgQFm2e88IuM_a/forum/w=580/sign=3b900475252eb938ec6d7afae56385fe/61954b10b912c8fcacb9f159f6039245d78821fd.jpg', 'https://ss2.bdstatic.com/70cFvnSh_Q1YnxGkpoWK1HF6hhy/it/u=991571279,1492844361&fm=26&gp=0.jpg','https://ss1.bdstatic.com/70cFvXSh_Q1YnxGkpoWK1HF6hhy/it/u=4227342398,2949207363&fm=26&gp=0.jpg'],
        random = Math.random(),
        index = Math.floor(random * (imgs.length));
    // console.log(random, '-index', index)
    return imgs[index] + "?" + Math.random()
}
export default class App extends Component {
    state = {
        url: ""
    }

    componentDidMount() {
        this.setState({ url: getImage() })
    }

    onReload = () => {
        this.setState({ url: getImage() })
    }

    render() {
        return (
            <div>
                <ImageCode
                    imageUrl={this.state.url}
                    onReload={this.onReload.bind(this)}
                    onMatch={() => {
                        console.log("code is match")
                    }}
                />
            </div>
        )
    }
}